import Vue from 'vue' // 导入Vue
import Router from 'vue-router' // 导入vue-router 库
import App from '@/App'
import Home from '@/components/home' // home.vue 组件,.vue可省略
import Resume from '@/components/resume'
import BusRecords from '@/components/busrecords'
import WinMine from '@/components/winmine'
Vue.use(Router) //全局注册Router组件，它会绑定到Vue实例里面。
export default new Router({ // 创建 router 实例，然后传 `routes` 配置
  routes: [{
      path: '/', //访问路径
      redirect: '/home',
      component: Home,
      name: 'Home', // 路径名
      children: [
        {
          path: 'home',
        }
    ]
    }, {
      path: '/resume', //访问路径
      name: 'Resume', // 路径名
      component: Resume //访问的组件,加载的内容
    }, {
      path: '/busrecords', //访问路径
      name: 'BusRecords', // 路径名
      component: BusRecords //访问的组件,加载的内容
    }, {
      path: '/winmine', //访问路径
      name: 'WinMine', // 路径名
      component: WinMine //访问的组件,加载的内容
    }
  ]
})



