<template>
<div>
  <el-row>
    <el-col :span="12">
      <el-card><div id="people-num-bar" class="chart"></div></el-card>
    </el-col>
    <el-col :span="12">
      <el-card><div id="people-num-pie" class="chart"></div></el-card>
    </el-col>
  </el-row>
  <el-row>
    <el-card><el-table :data="records" border>
      <el-table-column prop="date" label="日期"></el-table-column>
      <el-table-column prop="peopleNum" label="乘客规模"></el-table-column>
  </el-table></el-card>
  </el-row>
  </div>
</template>

<script>
  let echarts = require('echarts');
  export default {
    props: ['records', 'dateArray'],
    data() {
      return {
        peopleNumLevel: ['少', '少+', '中', '中+', '多', '多+']
      }
    },
    computed: {
      //乘客规模数组
      peopleNumArray: function() {
        return Array.from(this.records, elem => [elem.date, elem.peopleNum]);
      },
      //乘客规模数量对象
      peopleNumSum: function() {
        //['少', '少+', '中', '中+', '多', '多+']
        let tempObj = {
          few: 0,
          fewPlus: 0,
          middle: 0,
          middlePlus: 0,
          huge: 0,
          hugePlus: 0
        };
        for (let elem of this.records) {
          switch (elem.peopleNum) {
            case '少':
              tempObj.few++;
              break;
            case '少+':
              tempObj.fewPlus++;
              break;
            case '中':
              tempObj.middle++;
              break;
            case '中+':
              tempObj.middlePlus++;
              break;
            case '多':
              tempObj.huge++;
              break;
            default:
              tempObj.hugePlus++;
              break;
          }
        }
        return tempObj;
      },
      barOption: function() {
        return {
          title: {
            text: '986乘客规模'
          },
          legend: {
            data: ['乘客规模']
          },
          toolbox: {
            feature: {
              magicType: {
                type: ['line', 'bar']
              },
              saveAsImage: {}
            }
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/> {c}"
          },
          xAxis: {
            data: this.dateArray
          },
          yAxis: {
            type: 'category',
            data: this.peopleNumLevel
          },
          series: [{
            name: '乘客规模',
            type: 'bar',
            data: this.peopleNumArray
          }]
        }
      },
      pieOption: function() {
        return {
          title: {
            text: '986乘客规模'
          },
          legend: {
            data: ['少', '少+', '中', '中+', '多', '多+']
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          series: [{
            name: '乘客规模',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [{
              value: this.peopleNumSum.few,
              name: '少'
            }, {
              value: this.peopleNumSum.fewPlus,
              name: '少+'
            }, {
              value: this.peopleNumSum.middle,
              name: '中'
            }, {
              value: this.peopleNumSum.middlePlus,
              name: '中+'
            }, {
              value: this.peopleNumSum.huge,
              name: '多'
            }, {
              value: this.peopleNumSum.hugePlus,
              name: '多+'
            }],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }]
        }
      }
    },
    mounted: function() {
      this.drawPeopleNum()
    },
    methods: {
      //986乘客规模图
      drawPeopleNum: function() {
        echarts.init(document.getElementById('people-num-bar')).setOption(this.barOption);
        echarts.init(document.getElementById('people-num-pie')).setOption(this.pieOption);
      },
    }
  }

</script>

<style lang="less" scoped>
  @height: 600px;
  @width: 650px;
  .chart {
    height: @height;
    width: @width;
    max-height: @height + 200px;
    max-width: @width + 50px;
  }

</style>



