<template>
  <div>
    <div v-for="introduce in introduces" class="background" :style="introduce.background">
      <div class="content">
        <h1 v-text="introduce.title"></h1>
        <p v-text="introduce.content"></p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        introduces: [{
          title: '这是我的第一个个人网站作品',
          content: '经过了我的不懈努力，我的第一个网站作品雏形已经完成了，在我的网站里，我会介绍我的一些个人信息，这些信息可以用来在我面试的时候进行一些加分'
        }, {
          title: '我的技术展示',
          content: '在这里我会利用我所掌握的技术，比如说Vue.js框架、element-ui组件库、echarts.js数据可视化工具和CSS使用技巧等技术'
        }, {
          title: '我的公交记录',
          content: '在此我会使用百度的开源数据可视化工具echarts.js来展示我的公交记录'
        }]
      }
    }
  };

</script>
<style lang="less">
  /*内容区域高度*/
  
  @contentHeight: 500px;
  /*内容区域最大宽度*/
  
  @contentMaxWidth: 600px;
  /*内容区域上下内边距*/
  
  @contentPaddingTop: 80px;
  .background {
    background-size: 100% @contentHeight;
    background-repeat: no-repeat;
    height: @contentHeight - @contentPaddingTop * 2;
    width: @contentMaxWidth;
    padding: @contentPaddingTop calc(~'50% - @{contentMaxWidth} / 2');
    &:nth-child(1) {
      background-image: url(../assets/homePage/concert.jpg);
    }
    &:nth-child(2) {
      background-image: url(../assets/homePage/bridge.jpg);
    }
    &:nth-child(3) {
      background-image: url(../assets/homePage/bus.jpg);
    }
  }
  
  .content {
    background: hsla(0, 0%, 100%, .5);
    color: black;
    text-align: center;
    border-radius: 10px;
    padding: 10px;
    h1 {
      font-size: 30px;
    }
    p {
      font-size: 20px;
      margin: 10px auto;
    }
  }

</style>



