<template>
<footer>
  <!--系统名称-->
  <div class="main">
    <p class="main-title">张华一的个人网站v{{version}}</p>
    <a class="main-link" href="http://kirazhy.github.io/index.html" target="_blank">个人网站</a>
  </div>
  <!--个人信息-->
  <div class="social">
    <el-popover v-for="popover in popovers" placement="top" width="200" popper-class="popover" trigger="hover">
      <div class="popover-title" v-text="popover.title"></div>
      <p v-for="content in popover.contents" v-text="content"></p>
      <i class="fa fa-fw footericon" :class="popover.icon" slot="reference"></i>
  </el-popover>
    <a v-for="link in links" :href="link.href" target="_blank"><i class="fa fa-fw f footericon" :class="link.icon"></i></a>
  </div>
  </footer>
</template>

<script>
  export default {
    props: ['version'],
    data() {
      return {
        //页脚popover
        popovers: [{
          ref: 'contact',
          title: '联系方式',
          contents: [
            '电话:+86 13671250323', 'QQ:1039824096', '电子邮件:kirazhy@sina.com'
          ],
          icon: 'fa-phone'
        }, {
          ref: 'address',
          title: '公司地址',
          contents: [
            '北京办公室：朝阳区亮马桥路凯宾斯基酒店C417', "香港办公室：皇后大道中181号新纪元广场1501"
          ],
          icon: 'fa-address-book'
        }],
        links: [{
          href: 'https://github.com/kirazhy/kirazhy.github.io',
          icon: 'fa-github'
        }, {
          href: 'https://www.linkedin.com/in/%E5%8D%8E%E4%B8%80-%E5%BC%A0-930358142/',
          icon: 'fa-linkedin'
        }, {
          href: 'https://www.facebook.com/profile.php?id=100011141920148',
          icon: 'fa-facebook'
        }, {
          href: 'https://twitter.com/zhang_huayi',
          icon: 'fa-twitter'
        }, {
          href: 'https://www.instagram.com/huayizhang1412/',
          icon: 'fa-instagram'
        }]
      }
    }
  }

</script>

<style lang="less" scoped>
  @footerIconSize: 32px;
  @footerMaxWidth: 1200px;
  @footerHeight: 80px;
  /*页脚*/
  
  footer {
    height: @footerHeight;
    background-color: black;
    color: grey;
    max-width: @footerMaxWidth;
    padding: 30px calc(~'50% - @{footerMaxWidth} / 2');
  }
  /*左侧标题区*/
  
  .main {
    float: left;
    .main-title {
      line-height: 1;
      font-size: 22px;
      margin: 0;
    }
    .main-link {
      display: inline-block;
      margin-top: 8px;
      font-size: 12px;
      color: lightgray;
    }
  }
  /*右侧信息区*/
  
  .social {
    float: right;
    line-height: @footerHeight;
    .footericon {
      transition: .3s;
      display: inline-block;
      line-height: @footerIconSize;
      text-align: center;
      color: #8D99AB;
      background-color: transparent;
      margin-right: 20px;
      size: @footerIconSize;
      font-size: @footerIconSize;
      vertical-align: middle;
      &:hover {
        transform: scale(1.2);
        color: white;
      }
    }
  }
  
  .popover {
    padding: 0;
    min-width: 120px;
    line-height: normal;
    box-shadow: 0 0 11px 0 rgba(174, 187, 211, 0.24);
    .popover-title {
      border-bottom: solid 1px #eaeefb;
      height: 30px;
      line-height: 30px;
      text-align: center;
      color: #99a9bf;
      background-color: #f8f9fe;
    }
    p {
      text-align: center;
      margin: 10px;
    }
  }

</style>



