<template>
<div>
  <el-row>
    <el-col :span="12">
      <el-card><div id="congestion-degree-bar" class="chart"></div></el-card>
    </el-col>
    <el-col :span="12">
      <el-card><div id="congestion-degree-pie" class="chart"></div></el-card>
    </el-col>
  </el-row>
  <el-row>
    <el-card><el-table :data="records" border>
      <el-table-column prop="date" label="日期"></el-table-column>
      <el-table-column prop="congestionDegree" label="拥堵程度"></el-table-column>
  </el-table></el-card>
  </el-row>
  </div>
</template>

<script>
  let echarts = require('echarts');
  export default {
    props: ['records', 'dateArray'],
    data() {
      return {
        congestionDegreeLevel: ['极通畅', '通畅', '中', '拥堵', '极拥堵']
      }
    },
    computed: {
      //拥堵程度数组
      congestionDegreeArray: function() {
        return Array.from(this.records, elem => [elem.date, elem.congestionDegree]);
      },
      //拥堵程度数量对象
      congestionDegreeSum: function() {
        //['极通畅', '通畅', '中', '拥堵', '极拥堵']
        let tempObj = {
          veryUnobstructed: 0,
          unobstructed: 0,
          middle: 0,
          congestion: 0,
          veryCongestion: 0
        };
        for (let elem of this.records) {
          switch (elem.congestionDegree) {
            case '极通畅':
              tempObj.veryUnobstructed++;
              break;
            case '通畅':
              tempObj.unobstructed++;
              break;
            case '中':
              tempObj.middle++;
              break;
            case '拥堵':
              tempObj.congestion++;
              break;
            default:
              tempObj.veryCongestion++;
              break;
          }
        }
        return tempObj;
      },
      barOption: function() {
        return {
          title: {
            text: '十八里店桥拥堵程度'
          },
          legend: {
            data: ['拥堵程度']
          },
          toolbox: {
            feature: {
              magicType: {
                type: ['line', 'bar']
              },
              saveAsImage: {}
            }
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/> {c}"
          },
          xAxis: {
            data: this.dateArray
          },
          yAxis: {
            type: 'category',
            data: this.congestionDegreeLevel
          },
          series: [{
            name: '拥堵程度',
            type: 'bar',
            data: this.congestionDegreeArray
          }]
        }
      },
      pieOption: function() {
        return {
          title: {
            text: '十八里店桥拥堵程度'
          },
          legend: {
            data: ['极通畅', '通畅', '中', '拥堵', '极拥堵']
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          series: [{
            name: '拥堵程度',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [{
              value: this.congestionDegreeSum.veryUnobstructed,
              name: '极通畅'
            }, {
              value: this.congestionDegreeSum.unobstructed,
              name: '通畅'
            }, {
              value: this.congestionDegreeSum.middle,
              name: '中'
            }, {
              value: this.congestionDegreeSum.congestion,
              name: '拥堵'
            }, {
              value: this.congestionDegreeSum.veryCongestion,
              name: '极拥堵'
            }],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }]
        }
      }
    },
    mounted: function() {
      this.drawCongestionDegree()
    },
    methods: {
      //十八里店桥拥堵程度图
      drawCongestionDegree: function() {
        echarts.init(document.getElementById('congestion-degree-bar')).setOption(this.barOption);
        echarts.init(document.getElementById('congestion-degree-pie')).setOption(this.pieOption);
      },
    }
  }

</script>

<style lang="less" scoped>
  @height: 600px;
  @width: 650px;
  .chart {
    height: @height;
    width: @width;
    max-height: @height + 200px;
    max-width: @width + 50px;
  }

</style>



