<template>
<div>
  <!--页眉-->
  <header>
    <router-link :to="'home'"><img src="../assets/myPhoto.png"></router-link>
    <h1>Welcome to MY Zone</h1>
  </header>
  <!--导航栏-->
  <nav>
    <el-menu default-active="1" class="el-menu-demo" mode="horizontal">
      <el-menu-item v-for="nav in navs" :index="nav.index">
        <router-link :to="nav.path" ><i class="fa fa-lg" :class="nav.icon" @mouseenter="spinIcon" @mouseleave="stopIcon"></i>{{nav.title}}</router-link>
  </el-menu-item>
  </el-menu>
  </nav>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        navs: [{
          index: '1',
          icon: 'fa-home',
          title: '首页',
          path: 'home'
        }, {
          index: '2',
          icon: 'fa-address-card',
          title: '个人简历',
          path: 'resume'
        }, {
          index: '3',
          icon: 'fa-bus',
          title: '公交记录',
          path: 'busrecords'
        }, {
          index: '4',
          icon: 'fa-smile-o',
          title: '扫雷',
          path: 'winmine'
        }],
      }
    },
    methods: {
      //鼠标进入menu则i旋转
      spinIcon: function(event) {
        event.target.classList.add("fa-spin");
      },
      //鼠标离开menu则i停止
      stopIcon: function(event) {
        event.target.classList.remove("fa-spin");
      },
    }
  }

</script>

<style lang="less" scoped>
  @headerHeight: 100px;
  /*页眉*/
  
  @font-face {
    font-family: 'ChannelSlanted';
    src: url('//cdn.webfont.youziku.com/webfonts/nomal/101653/45817/58f80e62f629d81378fbf9ed.gif?r=41045218749');
    src: url('//cdn.webfont.youziku.com/webfonts/nomal/101653/45817/58f80e62f629d81378fbf9ed.gif?r=41045218749?#iefix') format('embedded-opentype'), url('//cdn.webfont.youziku.com/webfonts/nomal/101653/45817/58f80e62f629d81378fbf9ed.bmp?r=41045218749') format('woff'), url('//cdn.webfont.youziku.com/webfonts/nomal/101653/45817/58f80e62f629d81378fbf9ed.jpg?r=41045218749') format('truetype'), url('//cdn.webfont.youziku.com/webfonts/nomal/101653/45817/58f80e62f629d81378fbf9ed.png#ChannelSlanted2') format('svg');
  }
  
  header {
    width: 100%;
    height: @headerHeight;
    background-color: #20a0ff;
    color: white;
    position: relative;
    overflow: hidden;
    img {
      height: @headerHeight;
      float: left;
    }
    h1 {
      font-family: 'ChannelSlanted';
      font-size: 35px;
      line-height: @headerHeight;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  /*导航栏链接*/
  
  nav .el-menu-item a {
    display: block;
    height: 100%;
  }

</style>



