<template>
<div>
  <el-row>
    <el-col :span="12">
      <el-card><div id="seat" class="chart"></div></el-card>
    </el-col>
    <el-col :span="12">
      <el-card><el-table :data="seatRecords" border>
        <el-table-column prop="date" label="日期"></el-table-column>
        <el-table-column prop="seat" label="是否有座"></el-table-column>
    </el-table>
    </el-card>
    </el-col>
  </el-row>
  </div>
</template>

<script>
  let echarts = require('echarts');
  export default {
    props: ['records'],
    data() {
      return {}
    },
    computed: {
      //是否有座记录
      seatRecords: function() {
        return this.records.filter(function(elem) {
          return typeof elem.seat !== 'undefined';
        })
      },
      //是否有座数量对象
      seatSum: function() {
        //['有','无']
        let tempObj = {
          true: 0,
          false: 0
        };
        for (let elem of this.seatRecords) {
          elem.seat === '有' ? tempObj.true++ : tempObj.false++;
        }
        return tempObj;
      },
      option: function() {
        return {
          title: {
            text: '横街子前是否有座',
          },
          legend: {
            data: ['有', '无']
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          series: [{
            name: '是否有座',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [{
              value: this.seatSum.true,
              name: '有'
            }, {
              value: this.seatSum.false,
              name: '无'
            }],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }]
        }
      }
    },
    mounted: function() {
      this.drawSeat();
    },
    methods: {
      //横街子前是否有座图
      drawSeat: function() {
        echarts.init(document.getElementById('seat')).setOption(this.option);
      },
    }
  }

</script>

<style lang="less" scoped>
  @height: 600px;
  @width: 650px;
  .chart {
    height: @height;
    width: @width;
    max-height: @height + 200px;
    max-width: @width + 50px;
  }

</style>



