<template>
<div>
  <el-row>
    <el-col :span="12">
      <el-card><div id="bus-type-bar" class="chart"></div></el-card>
  </el-col>
    <el-col :span="12">
      <el-card><div id="bus-type-pie" class="chart"></div></el-card>
  </el-col>
  </el-row>
  <el-row>
    <el-card><el-table :data="busTypeRecords" border>
      <el-table-column prop="date" label="日期"></el-table-column>
      <el-table-column prop="busType" label="公交类型"></el-table-column>
  </el-table></el-card>
  </el-row>
  </div>
</template>

<script>
  let echarts = require('echarts');
  export default {
    props: ['records'],
    data() {
      return {
        busTypeLevel: ['地铁', '300', '300快']
      }
    },
    computed: {
      //公交类型记录
      busTypeRecords: function() {
        return this.records.filter(function(elem) {
          return typeof elem.busType !== 'undefined';
        })
      },
      //公交类型记录中的日期数组
      dateArray4BusType: function() {
        return Array.from(this.busTypeRecords, elem => elem.date);
      },
      //公交类型数组
      busTypeArray: function() {
        return Array.from(this.busTypeRecords, elem => [elem.date, elem.busType]);
      },
      //换乘前公交类型数量对象
      busTypeSum: function() {
        //['地铁', '300', '300快']
        let tempObj = {
          subway: 0,
          threeHundred: 0,
          threeHundredFast: 0,
        };
        for (let elem of this.busTypeRecords) {
          switch (elem.busType) {
            case '地铁':
              tempObj.subway++;
              break;
            case '300':
              tempObj.threeHundred++;
              break;
            default:
              tempObj.threeHundredFast++;
              break;
          }
        }
        return tempObj;
      },
      barOption: function() {
        return {
          title: {
            text: '换乘前公交类型'
          },
          legend: {
            data: ['公交类型']
          },
          toolbox: {
            feature: {
              magicType: {
                type: ['line', 'bar']
              },
              saveAsImage: {}
            }
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/> {c}"
          },
          xAxis: {
            data: this.dateArray4BusType
          },
          yAxis: {
            type: 'category',
            data: this.busTypeLevel
          },
          series: [{
            name: '公交类型',
            type: 'bar',
            data: this.busTypeArray
          }]
        }
      },
      pieOption: function() {
        return {
          title: {
            text: '换乘前公交类型'
          },
          legend: {
            data: ['地铁', '300', '300快']
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          series: [{
            name: '公交类型',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [{
              value: this.busTypeSum.subway,
              name: '地铁'
            }, {
              value: this.busTypeSum.threeHundred,
              name: '300'
            }, {
              value: this.busTypeSum.threeHundredFast,
              name: '300快'
            }],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }]
        }
      }
    },
    mounted: function() {
      this.drawBusType();
    },
    methods: {
      //换乘前公交类型图
      drawBusType: function() {
        echarts.init(document.getElementById('bus-type-bar')).setOption(this.barOption);
        echarts.init(document.getElementById('bus-type-pie')).setOption(this.pieOption);
      }
    }
  }

</script>

<style lang="less" scoped>
  @height: 600px;
  @width: 650px;
  .chart {
    height: @height;
    width: @width;
    max-height: @height + 200px;
    max-width: @width + 50px;
  }

</style>



