<template>
<div>
  <el-row>
      <el-card><div id="travel-time" class="chart"></div></el-card>
  </el-row>
  <el-row>
      <el-card><el-table :data="records" border>
        <el-table-column prop="date" label="日期"></el-table-column>
        <el-table-column prop="travelTime" label="路上时间" sortable></el-table-column>
  </el-table></el-card>
    </el-row>
  </div>
</template>

<script>
  let echarts = require('echarts');
  export default {
    props: ['records', 'dateArray'],
    data() {
      return {}
    },
    computed: {
      //路上时间数组
      travelTimeArray: function() {
        return Array.from(this.records, elem => elem.travelTime);
      },
      option: function() {
        return {
          title: {
            text: '路上时间'
          },
          legend: {
            data: ['路上时间']
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            },
            formatter: function(params) {
              params = params[0];
              return params.name + '<br>到家时间：' + (18 + Math.floor(params.value / 60)) + ':' + ((params.value % 60) >= 10 ? params.value % 60 : '0' + params.value % 60);
            }
          },
          toolbox: {
            feature: {
              magicType: {
                type: ['line', 'bar']
              },
              saveAsImage: {}
            }
          },
          xAxis: {
            data: this.dateArray
          },
          yAxis: {
            axisLabel: {
              formatter: '{value} min'
            }
          },
          series: [{
            name: '路上时间',
            type: 'bar',
            data: this.travelTimeArray
          }]
        }
      }
    },
    mounted: function() {
      this.drawTravelTime();
    },
    methods: {
      //路上时间图
      drawTravelTime: function() {
        echarts.init(document.getElementById('travel-time')).setOption(this.option);
      },
    }
  }

</script>

<style lang="less" scoped>
  @height: 600px;
  @width: 100vw;
  .chart {
    height: @height;
    width: @width;
    max-height: @height + 200px;
    max-width: @width + 50px;
  }

</style>



