<template>
<div>
  <el-row>
    <el-col :span="12">
      <el-card><div id="come-speed-bar" class="chart"></div></el-card>
    </el-col>
    <el-col :span="12">
      <el-card><div id="come-speed-pie" class="chart"></div></el-card>
  </el-col>
    </el-row>
  <el-row>
    <el-card><el-table :data="records" border>
        <el-table-column prop="date" label="日期"></el-table-column>
        <el-table-column prop="comeSpeed" label="到达速度"></el-table-column>
    </el-table></el-card>
  </el-row>
  </div>
</template>

<script>
  let echarts = require('echarts');
  export default {
    props: ['records', 'dateArray'],
    data() {
      return {
        comeSpeedLevel: ['快', '快+', '中', '中+', '慢', '慢+']
      }
    },
    computed: {
      //到达速度数组
      comeSpeedArray: function() {
        return Array.from(this.records, elem => [elem.date, elem.comeSpeed]);
      },
      //到达速度数量对象
      comeSpeedSum: function() {
        //['快', '快+', '中', '中+', '慢', '慢+']
        let tempObj = {
          fast: 0,
          fastPlus: 0,
          middle: 0,
          middlePlus: 0,
          slow: 0,
          slowPlus: 0
        };
        for (let elem of this.records) {
          switch (elem.comeSpeed) {
            case '快':
              tempObj.fast++;
              break;
            case '快+':
              tempObj.fastPlus++;
              break;
            case '中':
              tempObj.middle++;
              break;
            case '中+':
              tempObj.middlePlus++;
              break;
            case '慢':
              tempObj.slow++;
              break;
            default:
              tempObj.slowPlus++;
              break;
          }
        }
        return tempObj;
      },
      barOption: function() {
        return {
          title: {
            text: '986到达速度'
          },
          legend: {
            data: ['到达速度']
          },
          toolbox: {
            feature: {
              magicType: {
                type: ['line', 'bar']
              },
              saveAsImage: {}
            }
          },
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/> {c}'
          },
          xAxis: {
            data: this.dateArray
          },
          yAxis: {
            type: 'category',
            data: this.comeSpeedLevel
          },
          series: [{
            name: '到达速度',
            type: 'bar',
            data: this.comeSpeedArray
          }]
        }
      },
      pieOption: function() {
        return {
          title: {
            text: '986到达速度'
          },
          legend: {
            data: ['快', '快+', '中', '中+', '慢', '慢+']
          },
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} : {c} ({d}%)'
          },
          series: [{
            name: '到达速度',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [{
              value: this.comeSpeedSum.fast,
              name: '快'
            }, {
              value: this.comeSpeedSum.fastPlus,
              name: '快+'
            }, {
              value: this.comeSpeedSum.middle,
              name: '中'
            }, {
              value: this.comeSpeedSum.middlePlus,
              name: '中+'
            }, {
              value: this.comeSpeedSum.slow,
              name: '慢'
            }, {
              value: this.comeSpeedSum.slowPlus,
              name: '慢+'
            }],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }]
        }
      }
    },
    mounted: function() {
      this.drawComeSpeed();
    },
    methods: {
      //986到达速度图，type：1柱状图，type：2饼图
      drawComeSpeed: function() {
        echarts.init(document.getElementById('come-speed-bar')).setOption(this.barOption);
        echarts.init(document.getElementById('come-speed-pie')).setOption(this.pieOption);
      }
    }
  }

</script>

<style lang="less" scoped>
  @height: 600px;
  @width: 650px;
  .chart {
    height: @height;
    width: @width;
    max-height: @height + 200px;
    max-width: @width + 50px;
  }

</style>



